@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;800&display=swap);

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  padding-right: 0 !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  width: 100%;
  overflow-x: hidden;
}

.react-datetime-picker__wrapper{
  padding: 11px 18.5px;
  border-radius: 4px;
  border:1px solid #b3b3b3 !important;
}

.load{
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  font-size: 40px;
  line-height: 37px;
  text-transform: uppercase;
  color: #092f57;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%,-40%);
          transform: translate(-50%,-40%);
}
.contact {
  width: 100%;
}

.contactContainer {
  width: 93%;
  margin: auto;
  padding-top: 20px;
  padding-bottom: 40px;
}

.contactHeading {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.contactHeading img {
  height: 1px;
}

.contactHeading h1 {
  margin: 0 15px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  font-size: 30px;
  line-height: 37px;
  text-transform: uppercase;
  color: #092f57;
}

.contactContainer p {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 170.6%;
  text-align: center;
  color: #092f57;
  width: 80%;
  margin: auto;
  margin-top: 30px;
  margin-bottom: 30px;
}

.contactInfo {
  width: 90%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: auto;
}

.contactBox {
  width: 33%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-align-items: center;
          align-items: center;
}

.contactBox img {
  width: 80px;
  height: 80px;
  margin-bottom: 20px;
  margin-top: 20px;
}

.contactBox a {
  text-align: center;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-transform: lowercase;
  color: #092f57;
  text-decoration: none;
}

.contactlocBox {
  width: 35%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-align-items: center;
          align-items: center;
}

.contactlocBox img {
  width: 80px;
  height: 80px;
  margin-bottom: 20px;
  margin-top: 20px;
}

.contactlocBox a {
  text-align: center;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-transform: lowercase;
  color: #092f57;
  text-decoration: none;
}

